
@include tablet {

    .seccion-inferior {

        display: grid;
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;
    }
}
.texto-entrada {

    a {
        color: $negro;
        margin-top: 2rem;
        display: block;

        @include tablet {
            margin-top: 0;
        }
    }

    h4 {
        margin: 0;
        line-height: 1.4;

        &::after {

            content: "";
            display: block;
            width: 15rem;
            height: .5rem;
            background-color: $verde;
            margin-top: 1rem;
        }
    }

}

.entrada-blog {

    margin-bottom: 2rem;

    @include tablet {
        display: grid;
        grid-template-columns: 1fr 2fr; //para acomodar img y texto en mq
        column-gap: 2rem;
        align-items: center;
    }
    &:last-of-type {
        margin-bottom: 0;
    }
}

.informacion-meta {
    span {
        color: $amarillo;
    }
}

