
.no-webp .img-contacto {
    background-image: url(../img/encuentra.jpg);
}
.webp .img-contacto {
    background-image: url(../img/encuentra.webp);
}

.img-contacto {
    background-position: center center;
    background-size: cover;
    padding: 5rem 0;

    display: flex;
    flex-direction: column;
    align-items: center;

    h2 {
        font-size: 4rem;
        font-weight: $black;
        color: $blanco;
    }
    p { 
        font-size: 1.8rem;
        color: $blanco;
        text-align: center;
    }
}